const MissionCard = ({ title, description, iconBgClass, iconPath, footerText, distance, duration, location }) => {
  return (
    <div className="mission-card">
      <div className={`icon ${iconBgClass}`}>
        <img src={iconPath} alt="" />
      </div>
      <div className="mission-content">
        <h3>{title}</h3>
        <p>{description}</p>
        {location && <p className="location-name">: {location}</p>}
        {distance && duration && (
          <p className="location-info">  : {distance}km |  {duration}</p>
        )}
        <p className="footer-text">{footerText}</p>
      </div>
    </div>
  );
};

export default MissionCard
