
import React, { useEffect, useRef, useState } from 'react';
import './MainPage.css'
import MissionCard from './components/MissionCard'
import QuestCard from './components/QuestCard'
import RouteCard from './components/RouteCard'
import QuestProgressItem from './components/QuestProgressItem'

const App = () => {
    const [selectedRoute, setSelectedRoute] = useState(null);
    const [isQuestStarted, setIsQuestStarted] = useState(false);
    const [selectedProgram, setSelectedProgram] = useState(null); // {title, distance, duration, location, iconPath}
    const baseImgUrl = process.env.PUBLIC_URL+'/assets/images/'

    const tourismRoutes = [
        {
            id: 'jomunguk',
            title: '  ',
            description: '   ,   ',
            iconBgClass: 'icon-circle light-blue',
            iconPath: 'M13.5 6a.5.5 0 11-1 0 .5.5 0 011 0zM12 2a10 10 0 100 20 10 10 0 000-20zM12 11a1 1 0 10-2 0v2l2 2 2-2v-2a1 1 0 10-2 0z',
            quests: [
                { title: '', subtitle: '   ', status: 'completed' },
                { title: '', subtitle: '    ', status: 'ongoing' },
                { title: '', subtitle: '  3  ', status: 'locked' },
                { title: '', subtitle: '      ', status: 'locked' },
                { title: '  ', subtitle: '     !', status: 'locked' },
            ]
        },
        {
            id: 'sanchoon-story',
            title: ' ',
            description: '      ',
            iconBgClass: 'icon-circle yellow',
            iconPath: 'M13 10V3L4 14h7v7l9-11h-7z',
            quests: [
                { title: '  ', subtitle: ' ', status: 'ongoing' },
                { title: ' ', subtitle: '  ', status: 'locked' },
                { title: ' ', subtitle: ' ', status: 'locked' },
            ]
        },
        {
            id: 'festival-gabaek',
            title: '  ',
            description: '      !',
            iconBgClass: 'icon-circle green',
            iconPath: 'M12 22s8-4 8-10V5l-8-3-8 3v7c0 6 8 10 8 10z',
            quests: [
                { title: ' ', subtitle: 'QR   ', status: 'completed' },
                { title: '  ', subtitle: '  ', status: 'completed' },
                { title: '  ', subtitle: '  ', status: 'ongoing' },
                { title: ' ', subtitle: '  ', status: 'locked' },
            ]
        },
        {
            id: 'historic-churches',
            title: ' 100   ',
            description: ' 6.25       ',
            iconBgClass: 'icon-circle red',
            iconPath: 'M12 2L2 7h20L12 2zM2 9v11h7V9H2zm13 0v11h7V9h-7z',
            quests: [
                { title: '', subtitle: '100     ', status: 'ongoing' },
                { title: '', subtitle: '6.25     ', status: 'locked' },
                { title: '', subtitle: '      ', status: 'locked' },
                { title: ' ', subtitle: '     ', status: 'locked' },
            ]
        },
    ];

    const handleStartQuest = () => {
        setIsQuestStarted(true);
    };

    const handleBack = () => {
        setIsQuestStarted(false);
        setSelectedRoute(null);
    };

    const handleAddProgram = (program) => {
        if (selectedProgram) return; //   :      
        setSelectedProgram(program);
    };

    const handleRemoveProgram = () => {
        setSelectedProgram(null);
    };

    return (
        <div
            className="main-container"
            style={{
                backgroundImage: `url(${process.env.PUBLIC_URL}/assets/images/hero_sky_fields.jpg)`,
                backgroundSize: 'cover',
                backgroundPosition: 'center',
                backgroundRepeat: 'no-repeat',
                minHeight: '100vh'
            }}
        >
            {/* Header */}
            <header className="header">
                <div className="logo-section">
                  <div className="logo-icon">
                    <img src={process.env.PUBLIC_URL+'/assets/images/uiseong_bi_ssal.png'} />
                  </div>
                  <div className="logo-texts">
                    <div className="logo-text-main"> ?</div>
                    <div className="logo-text-sub">   </div>
                  </div>
                </div>
                <div className="header-icons">
                    <div className="icon-wrapper">
                        {/* Notification Bell Icon */}
                        <svg className="icon" fill="currentColor" viewBox="0 0 20 20">
                            <path d="M10 2a6 6 0 00-6 6v3.586l-.707.707A1 1 0 004 14h12a1 1 0 00.707-1.707L14 11.586V8a6 6 0 00-6-6zM10 18a3 3 0 01-3-3h6a3 3 0 01-3 3z" clipRule="evenodd" fillRule="evenodd"></path>
                        </svg>
                        <span className="notification-dot"></span>
                    </div>
                    <div className="profile">
                        <img className="profile-img" src="https://placehold.co/40x40/6B7280/FFFFFF?text=P" alt="Profile Picture" />
                        <span className="profile-name"></span>
                    </div>
                </div>
            </header>

            {/* User Info & Points Section */}
            <section className="user-info-section">
                <div className="user-info-content">
                    <div className="user-details">
                        <div className="user-avatar-wrapper">
                            {/* User Avatar Icon */}
                            <svg className="avatar-icon" fill="currentColor" viewBox="0 0 24 24">
                                <path d="M12 12c2.21 0 4-1.79 4-4s-1.79-4-4-4-4 1.79-4 4 1.79 4 4 4zm0 2c-2.67 0-8 1.34-8 4v2h16v-2c0-2.66-5.33-4-8-4z"></path>
                        </svg>
                            <span className="level-badge"></span>
                        </div>
                        <div className="user-info-text">
                            <h2></h2>
                            <p> : 2025-08-05</p>
                        </div>
                    </div>
                    <div className="points-summary">
                        <div className="point-item">
                            <span> </span>
                            <span className="current-points">3,000 P</span>
                        </div>
                        <div className="point-item">
                            <span>  </span>
                            <span className="total-spending">5,600 </span>
                        </div>
                    </div>
                </div>
            </section>

            {/* Main Content Grid */}
            <div className="main-content-grid">
                {/* Left Side - Mission & Route Selection */}
                <div className="left-panel">
                    {selectedRoute ? (
                        !isQuestStarted ? (
                            <section className="mission-section">
                                <div className="section-header">
                                    <h3>{selectedRoute.title}</h3>
                                    <button onClick={handleBack} className="back-button"> </button>
                                </div>
                                <div className="mission-grid">
                                    {selectedRoute.quests.map((quest, index) => (
                                        <QuestCard 
                                            key={index}
                                            title={quest.title} 
                                            description={quest.subtitle} 
                                            iconBgClass={selectedRoute.iconBgClass} 
                                            iconPath={selectedRoute.iconPath} 
                                            status={quest.status}
                                        />
                                    ))}
                                </div>
                                <button onClick={handleStartQuest} className="start-button"> </button>
                            </section>
                        ) : (
                            <section className="mission-section">
                                {/*     (  )    /  */}
                                {(() => {
                                    const experiencePrograms = [
                                        { title: '', distance: '2.2', duration: 20, location: ''  , iconPath:baseImgUrl+'/uiseong_cafe.jpeg' },
                                        { title: '', distance: '1.5', duration: 12, location: ' 1' , iconPath:baseImgUrl+'/uiseong_cj.jpg' },
                                        { title: ' ', distance: '3.0', duration: 25, location: '' , iconPath:baseImgUrl+'/uiseong_mak.jpeg' },
                                        { title: '(Tea)', distance: '2.7', duration: 18, location: '' , iconPath:baseImgUrl+'/uiseong_gz.jpg' },
                                        { title: '', distance: '1.1', duration: 10, location: '' , iconPath:baseImgUrl+'/uiseong_model.jpeg' },
                                        { title: ' ', distance: '0.9', duration: 7, location: '' , iconPath:baseImgUrl+'/uiseong_jewal.png' },
                                        { title: '', distance: '2.9', duration: 22, location: ' ' , iconPath:baseImgUrl+'/uiseong_jadu.jpg' },
                                    ];
                                    return (
                                        <>
                                            <div className="section-header">
                                                <h3>  </h3>
                                                <button
                                                    onClick={handleBack}
                                                    style={{
                                                        color: 'red',
                                                        padding: '10px 20px',
                                                        border: '1px solid red',
                                                        borderRadius: '5px',
                                                        backgroundColor: 'transparent'
                                                    }}
                                                >
                                                      
                                                </button>
                                            </div>
                                            <div className="mission-grid">
                                                {experiencePrograms.map((program, index) => {
                                                    const isDisabled = !!selectedProgram && selectedProgram.title !== program.title;
                                                    const isSelected = !!selectedProgram && selectedProgram.title === program.title;
                                                    return (
                                                        <div
                                                            key={index}
                                                            onClick={() => { if (!selectedProgram) handleAddProgram(program); }}
                                                            style={{
                                                                position: 'relative',
                                                                opacity: isDisabled ? 0.5 : 1,
                                                                pointerEvents: isDisabled ? 'none' : 'auto'
                                                            }}
                                                        >
                                                            <MissionCard 
                                                                title={program.title}
                                                                description={`${program.title}  !`}
                                                                iconBgClass="icon-circle green"
                                                                iconPath={program.iconPath}
                                                                distance={program.distance}
                                                                duration={program.duration}
                                                                location={program.location}
                                                            />
                                                            {isSelected && (
                                                                <div className="selected-badge"></div>
                                                            )}
                                                        </div>
                                                    );
                                                })}
                                            </div>
                                        </>
                                    );
                                })()}
                            </section>
                        )
                    ) : (
                        <>
                            {/* Online Missions Section */}
                            <section className="mission-section">
                                <div className="section-header">
                                    <h3> </h3>
                                    <a href="#"> </a>
                                </div>
                                <div className="mission-grid">
                                    {/* Daily Quiz Card */}
                                    <MissionCard 
                                        title=" " 
                                        description="    !" 
                                        iconBgClass="icon-circle blue" 
                                        iconPath={process.env.PUBLIC_URL+'/assets/images/uiseong_bi_ssal.png'}
                                        footerText="08:56:30"
                                        />
                                    {/* SNS Promotion Card */}
                                    <MissionCard 
                                        title="SNS " 
                                        description="     !" 
                                        iconBgClass="icon-circle blue" 
                                        iconPath={process.env.PUBLIC_URL+'/assets/images/uiseong_bi_manl.png'}
                                        />
                                    {/* Survey Card */}
                                    <MissionCard 
                                        title="" 
                                        description="     !" 
                                        iconBgClass="icon-circle blue" 
                                        iconPath={process.env.PUBLIC_URL+'/assets/images/uiseong_gochu.png'}
                                    />
                                </div>
                            </section>

                            {/* Offline Route Selection Section */}
                            <section className="mission-section">
                                <div className="section-header">
                                    <h3>  </h3>
                                    <a href="#"> </a>
                                </div>
                                <div className="mission-grid">
                                    {tourismRoutes.map((route) => (
                                        <RouteCard
                                            key={route.id}
                                            title={route.title}
                                            description={route.description}
                                            iconBgClass={route.iconBgClass}
                                            iconPath={route.iconPath}
                                            onClick={() => setSelectedRoute(route)}
                                        />
                                    ))}
                                </div>
                            </section>
                        </>
                    )}
                </div>

                {/* Right Side - Quest Progress & Map */}
                <div className="right-panel">
                    {/* Current Quest Progress */}
                    <section className="quest-progress-section">
                        <div className="section-header">
                            <h3>{isQuestStarted && selectedRoute ? '  ' : '   '}</h3>
                        </div>
                        <ul className="quest-list">
                            {isQuestStarted && selectedRoute ? (
                                selectedRoute.quests.map((quest, index) => (
                                    <QuestProgressItem 
                                        key={index}
                                        title={quest.title} 
                                        subtitle={quest.subtitle} 
                                        status={quest.status}
                                    />
                                ))
                            ) : (
                                <>
                                    <li className="quest-item locked">
                                        <div className="quest-status locked">
                                            <svg xmlns="http://www.w3.org/2000/svg" className="w-4 h-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                                                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M12 15v2m-6 4h12a2 2 0 002-2v-6a2 2 0 00-2-2H6a2 2 0 00-2 2v6a2 2 0 002 2z"></path>
                                            </svg>
                                        </div>
                                        <div className="quest-details">
                                            <h4>  </h4>
                                            <p>    .</p>
                                        </div>
                                    </li>
                                </>
                            )}
                        </ul>
                        {/* Selected Experience Program (only one) */}
                        {selectedProgram ? (
                            <div className="selected-program">
                                <div className="sp-info">
                                    <strong>{selectedProgram.title}</strong>
                                    <div className="sp-meta">{selectedProgram.location}  {selectedProgram.distance}km  {selectedProgram.duration}</div>
                                </div>
                                <button className="sp-remove" onClick={handleRemoveProgram}></button>
                            </div>
                        ) : (
                            <div className="selected-program empty">  1  .</div>
                        )}
                    </section>
                    
                </div>
            </div>

            {/* Footer */}
            <footer className="footer">
                <p>&copy; 2025 Uiseong Tourism. All rights reserved.</p>
            </footer>

        </div>
    );
};


